export function filter(user) {
  let menu = { items: [] };

  if (user.username === "administrator")
    menu.items.push(
      {
        name: "Dashboard",
        url: "/dashboard",
        icon: "icon-speedometer",
        badge: {
          variant: "primary"
        }
      },
      {
        title: true,
        name: "Web"
      },
      {
        name: "Users",
        url: "/users/list",
        icon: "icon-people"
      },
      {
        name: "Invoices",
        url: "/invoices",
        icon: "icon-layers",
        children: [
          {
            name: "Pending",
            url: "/invoices/pending",
            icon: "icon-minus"
          },
          {
            name: "Paid",
            url: "/invoices/paid",
            icon: "icon-plus"
          }
        ]
      },
      {
        name: "Rewards",
        url: "/rewards/list",
        icon: "fa fa-dollar"
      },
      {
        name: "Withdrawals",
        url: "/withdrawals/list",
        icon: "icon-cursor"
      },
      {
        name: "Reports",
        url: "/reports/list",
        icon: "icon-layers"
      },
      {
        name: "Binary Points",
        url: "/points/list",
        icon: "icon-calculator"
      },
      {
        name: "Subscriptions",
        url: "/subscriptions/list",
        icon: "icon-book-open"
      },
      {
        name: "Vaults",
        url: "/vaults/list",
        icon: "icon-wallet"
      },
      {
        title: true,
        name: "System"
      },
      {
        name: "Configuration",
        url: "/configuration/list",
        icon: "fa fa-code"
      },
      {
        name: "Media",
        url: "/media/list",
        icon: "icon-screen-tablet"
      }
    );

  if (user.username === "activaciones") {
    menu.items.push({
      name: "Invoices",
      url: "/invoices",
      icon: "icon-layers",
      children: [
        {
          name: "Pending",
          url: "/invoices/pending",
          icon: "icon-minus"
        },
        {
          name: "Paid",
          url: "/invoices/paid",
          icon: "icon-plus"
        }
      ]
    });
  }

  if (user.username === "reportes") {
    menu.items.push(
      {
        name: "Dashboard",
        url: "/dashboard",
        icon: "icon-speedometer",
        badge: {
          variant: "primary"
        }
      },
      {
        name: "Reports",
        url: "/reports/list",
        icon: "icon-layers"
      }
    );
  }
  return menu;
}
